<script>
import { reactive } from "vue";
import { SlickList, SlickItem, HandleDirective, Icon } from "vue-slicksort";
import { Parser } from "bbcodejs";
import { HTML2BBCode } from "html2bbcode";

let _GEN_CLASS = "gen";

var converter = new HTML2BBCode({});

var parser = new Parser();
jQuery.fn.selectText = function () {
  var doc = document;
  var element = this[0];
  //console.log(this, element);
  if (doc.body.createTextRange) {
    var range = document.body.createTextRange();
    range.moveToElementText(element);
    range.select();
  } else if (window.getSelection) {
    var selection = window.getSelection();
    var range = document.createRange();
    range.selectNodeContents(element);
    selection.removeAllRanges();
    selection.addRange(range);
  }
};

function findAll(re, str) {
  let ret = [];
  let array1;
  while ((array1 = re.exec(str)) !== null) {
    // Sadge hack to fix groups
    if (array1.groups.tag.toLowerCase() == "repeat") {
      array1.groups.repeat = array1.groups.tag;
      array1.groups.repeat_val = array1.groups.tag_val;
    }
    ret.push({ text: array1[0], groups: array1.groups, idx: re.lastIndex });
  }
  return ret;
}

function htmlDecode(input) {
  //console.log(input);
  var doc = new DOMParser().parseFromString(input, "text/html");
  $(doc)
    .find("div.single-item")
    .each((n, e) => {
      //console.log($(e).data('id'))
    });
  return doc.documentElement.textContent;
}

/* Store repeated text in this format:
{
  before: "text before repeated section",
  after: "text after repeated section",
  repeat: string or recursive structure {before: ..., after:...}
  repeat-n: Number of time to repeat (int or -1 if expand repeats)
}
*/


class ItemCounter {
  constructor(items, replace, attr) {
    this.i = 0;
    this.items = items;
    this.replace = replace; // Substring to replace with attr
    this.attr = attr;  // Element on item to replace `replace`
  }

  substitute(str) {

    var replaced = false;
    var found_key = false;
    let s = str.replace(this.replace, (token)=>  {
      found_key = true;
      if(this.i < this.items.length){
        replaced = true;
        return this.items[this.i++][this.attr]
      }
      return this.replace;
    });
    return {s, replaced, found_key}
  }
}

class RepeatTextFormat {
  constructor() {
    this.children = [];
    this.repeat_n = 1; // number of times to repeat myself.]
    this.parent = null;
    this.error = null;
  }

  str(depth) {
    if (this.error) {
      return this.error;
    }
    let ret = "";

    if (!depth) depth = 0;
    let indent = "  ".repeat(depth);

    let skip_indent = false;
    if (this.repeat_n != 1) {
      ret = `${indent}${this.repeat_n} x `;
      skip_indent = true;
    }

    this.children.forEach(e => {
      if (skip_indent) skip_indent = false;
      else ret += indent;

      if (e instanceof RepeatTextFormat) {
        let e_str = e.str(depth + 1);
        if (e_str.length == 0) return;
        ret += `${e_str}`;
      } else {
        if (e.length == 0) return;
        ret += `${e}\n`;
      }
    });
    return ret;
  }

  expand(items) {
    // Compute variable-length (global) repeat
    let queue = [this];
    let visited = new Set();
    let global_repeat;
    let factor = 1;
    while (queue.length > 0) {
      let format = queue.pop();
      visited.add(format);

      if (format.repeat_n == -1) global_repeat = format;
      else factor *= format.repeat_n;

      for (let c of format.children) {
        if (c instanceof RepeatTextFormat && !visited.has(c)) {
          queue.push(c);
        }
      }
    }
    let global_repeat_n = Math.ceil(items.length / factor);

    let counters = [
      new ItemCounter(items, "$NAME2", "item_name"),
      new ItemCounter(items, "$NAME", "item_name"),
      new ItemCounter(items, "$ID", "item_id"),
      new ItemCounter(items, "$NUM", "item_quant"),
      new ItemCounter(items, "$MAX", "item_max"),
      new ItemCounter(items, "$IMG", "item_img"),
      new ItemCounter(items, "$TRADE", "trade")]

    let ret = this.expand_helper(items, global_repeat_n, counters);

    return ret.str;
  }

  expand_helper(items, global_repeat_n, counters) {
    let is_leaf = !this.children.some(e => e instanceof RepeatTextFormat);
    let repeat_n = this.repeat_n;
    if (repeat_n == -1) repeat_n = global_repeat_n;
    let ret = "";
    for (var i = 0; i < repeat_n; i++) {
      let replace_str = "";

      this.children.forEach(e => {
        if (e instanceof RepeatTextFormat) {
          let expanded = e.expand_helper(items, global_repeat_n, counters);
          ret += expanded.str;
        } else {
          replace_str = e;

          let replaced_some = false;
          let found_key = false;
          for(let c in counters) {
            let sub = {replaced: true};
            while (sub.replaced == true) {
              sub = counters[c].substitute(replace_str);
              if (sub.replaced == false) {
                found_key |= sub.found_key;
                break;
              }
              replaced_some = true;
              replace_str = sub.s;
            }
          }
          if(!found_key || replaced_some) ret += replace_str;
        }
      });




    }
    return { str: ret, index: 1 };
  }
}

export default {
  name: "App",
  props: {
    html_input: String
  },
  computed: {
    text_format: function () {
      let format = this.bbformat;
      if (format == "custom") {
        this.show_custom = true;
        format = this.customformat;
      } else {
        this.customformat = this.bbformat;
      }
      // tags can be [tag_name] or [tag_name=value] or [tag_name repeat=#]
      let tag_re =
        /\[(?<end>\/)?(?<tag>[a-z]+)(?:=(?<tag_val>[^\] ]+))?(?:[ ]+(?<repeat>repeat)(?:=(?<repeat_val>[^\] ]+))?)?]/gi;
      let all_tags = findAll(tag_re, format);

      //console.log("all tags:", all_tags);

      let global_repeat;
      let open_tags = [];
      let repeat_tags = [];

      let idx = 0;
      let cur_text = "";

      let text_format = new RepeatTextFormat();
      //text_format.parent = text_format;
      let parent_format = text_format;

      let idx_to_text_format = {};

      all_tags.every(e => {
        //console.log("processing", e);
        let e_start_idx = e.idx - e.text.length;

        if (idx < e_start_idx) {
          // Handle text between tags.
          cur_text += format.substring(idx, e_start_idx);
          //console.log(`Text before ${e.text}: `, format.substring(idx, e_start_idx))
        }
        idx = e.idx;

        if (!e.groups.end) {
          // Case 1: open tag
          if(e.groups.tag !="item") {
            open_tags.push(e);
          }
          if (e.groups.repeat) {
            // Case 1a: open repeat tag
            let repeat_n;

            if (e.groups.repeat_val) {
              // regular repeat
              repeat_n = parseInt(e.groups.repeat_val);
              if(repeat_n <= 0) repeat_n = 1;
            } else {
              // global tag
              if (global_repeat) {
                text_format.error =
                  "Invalid format: expected a single [tag repeat]. Multiple [tag repeat=N] are allowed, only [repeat] is limited to a max of one.";
                return false;
              }
              global_repeat = e;
              repeat_n = -1;
            }

            let new_format = new RepeatTextFormat();
            new_format.parent = parent_format;
            new_format.repeat_n = repeat_n;
            //text_format.push(new_format);
            //text_format[text_format.length-2].repeat = new_format;
            if (cur_text.length > 0) parent_format.children.push(cur_text);
            parent_format.children.push(new_format);
            parent_format = new_format;
            idx_to_text_format[e.idx] = new_format;
            repeat_tags.push(e);
            cur_text = "";
          }
          // else {  // Case 1b: open normal tag
          //   // Add to cur_text like normal.
          //   cur_text += e.text;
          // }

          if (e.groups.tag.toLowerCase() == "repeat") {
            // pass
          } else if (e.groups.tag_val) {
            cur_text += `[${e.groups.tag}=${e.groups.tag_val}]`;
          } else {
            cur_text += `[${e.groups.tag}]`;
          }
        } else {
          // Case 2: closed tags

          // Find closest open tag
          let len_closed_tags = 1;
          let found_tag = false;
          //console.log('open tags:',open_tags, open_tags.length);
          for (var i = open_tags.length - 1; i >= 0; i--) {

            if (open_tags[i].groups.tag == e.groups.tag) {
              found_tag = open_tags[i];
              break;
            } else {
              len_closed_tags += 1;
            }
          }
          if (!found_tag) {
            len_closed_tags = 1; // Don't autoclose any tags.
          }

          let tags_to_close = open_tags.splice(-len_closed_tags);

          if (len_closed_tags > 1) console.log("autoclosing: ", tags_to_close);

          for (var i = tags_to_close.length - 1; i >= 0; i--) {
            if (e.groups.tag.toLowerCase() != "repeat") {
              cur_text += `[/${tags_to_close[i].groups.tag}]`;
            }

            // Update closing tags
            if (tags_to_close[i].groups.repeat) {
              // let format = idx_to_text_format[tags_to_close[i].idx];
              let cur_format = parent_format;
              if (cur_text.length > 0) cur_format.children.push(cur_text);
              parent_format = parent_format.parent;
              cur_text = "";
              repeat_tags.pop();
            }
          }
        }

        return true;
      });


      text_format.children.push(cur_text);
      text_format.children.push(format.slice(idx));

      //format="1";
      console.debug("Text format: ", text_format);
      console.debug("str:", text_format.str());

      return text_format;
    },
    bbcode: function () {
      return this.text_format.expand(this.items);
    },
    bbcode0: function () {
      let bb = "[center][table][tr]";
      this.items.forEach((e, n) => {
        bb += `[td]`;
        let showName = this.opt.bbName && e.item_name && e.item_name.length > 0;
        let url = "";
        if (e.item_name && e.item_name.length > 0) {
          let encoded_url = encodeURIComponent(e.item_name).replace("'", "%27");
          url = `http://dappervolk.com/trades?search=${encoded_url}&in=item&type=all&item_type=&rarity=all&order=newest`;
        }

        if (showName || url.length == 0) {
          bb += `[img]${e.item_img}[/img]`;
        } else {
          bb += `[url=${url}][img]${e.item_img}[/img][/url]`;
        }

        if (this.opt.bbQuant) {
          bb += ` x ${e.item_quant}`;
        }

        if (showName) bb += `\n[url=${url}]${e.item_name}[/url]`;
        bb += `[/td]`;
        if ((n + 1) % 3 == 0) {
          bb += "[/tr][/table][table][tr]";
        }
      });
      bb += "[/tr][/table][/center]";
      return bb;
      //return converter.feed(this.gen_html);
    },
    gen_html: function () {
      return parser.toHTML(this.bbcode);
    },
    preview: function () {
      return {
        template: `<div>${this.gen_html}</div>`,
        methods: {
          someAction() {
            console.log("Action!");
          }
        }
      };
    }
  },
  errorCaptured: function (err) {
    console.log("Caught error", err.message);
  },
  data() {
    let presets = [
      {
        name: "Fancy table (3 col)",
        value:
        "[center][repeat][table REPEAT=1][tr] [td REPEAT=3][url=$TRADE]$NAME\n[counter=$NUM][item=$NAME2][/counter][/url][/td][/tr][/table][/repeat][/center]"
          //"[center][repeat][table REPEAT=1][tr] [td REPEAT=3][img]$IMG[/img] x $NUM \n[url=$TRADE]$NAME[/url][/td][/tr][/table][/repeat][/center]"
      },
