      // img_inven_1: require('@/assets/img/inventory-1.png'),
      // img_inven_2: require('@/assets/img/inventory-2.png'),
      // img_paste: require('@/assets/img/paste.png'),

      defaultd: `\n\n<div class="form-group single-item" data-id="2374" data-stack="3" data-toggle="tooltip" data-placement="top" title="Iced Milk Coffee" style="box-sizing: border-box; margin: 2px; display: inline-block; vertical-align: middle; color: rgb(185, 140, 117); font-family: Roboto, Helvetica, Arial, sans-serif; font-size: 14px; font-style: normal; font-variant-ligatures: normal; font-variant-caps: normal; font-weight: 400; letter-spacing: normal; orphans: 2; text-align: start; text-indent: 0px; text-transform: none; white-space: normal; widows: 2; word-spacing: 0px; -webkit-text-stroke-width: 0px; background-color: rgb(248, 208, 161); text-decoration-thickness: initial; text-decoration-style: initial; text-decoration-color: initial;"><label for="2374" style="box-sizing: border-box; display: inline-block; max-width: 100%; margin-bottom: 5px; font-weight: 700;"><div class="inventory-item" style="box-sizing: border-box; background: none; position: relative; width: 55px;"><img src="https://dappervolk.com/img/items/2/2374-tqSnM3cCgr-iced-milk-coffee.png" alt="Iced Milk Coffee" style="box-sizing: border-box; border: 0px; vertical-align: middle; max-width: 55px; max-height: 55px; margin-right: 5px;"></div></label><select class="form-control" name="quantity[2374]" style="box-sizing: border-box; color: rgb(85, 85, 85); font-style: inherit; font-variant: inherit; font-weight: inherit; font-stretch: inherit; font-size: 14px; line-height: 1.42857; font-family: inherit; font-optical-sizing: inherit; font-kerning: inherit; font-feature-settings: inherit; font-variation-settings: inherit; margin: 0px 5px; text-transform: none; display: inline-block; width: auto; height: 34px; padding: 6px 12px; background: rgb(255, 255, 255); border: 1px solid rgb(225, 171, 135); border-radius: 4px; box-shadow: none; transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s; vertical-align: middle;"><option selected="" style="box-sizing: border-box;">1</option><opti