  methods: {
    editCustomFormat() {
      this.bbformat = "custom";
    },
    onEdit(evt) {
      //console.log("on edit", evt)
      var src = this.$refs.inp.innerHTML;
      this.updateHTML(src);
    },
    endEdit() {
      //console.log("end edit", evt)
      this.$el.querySelector(".editme").blur();
    },
    onLiveEdit(evt) {
      // console.log("live edit", evt)
      // console.log(evt.target);
      if ($(evt.target).hasClass("item-name")) return false;
      var src = this.$refs.inp.innerHTML;

      this.updateHTML(src);
    },
    updateHTML(src) {
      this.getItems();
      //bbcodejs.Parser();
    },
    getItems() {
      let gen_html = "";

      let gen_textarea = "";
      let items = [];

      let cur_info = {};

      $(this.$refs.inp)
        .find("*")
        .each((n, e) => {
          let $e = $(e);
          if ($e.is("div.single-item")) {
            // Selected from inventory side bar.
            let item_name = $e.attr("title");
            if (item_name.length == 0) item_name = $e.data("original-title");
            //let item_id = $e.data('id');
            //let item_quant = $e.find('select').val();
            let item_max = $e.find("select option").last().val();
            let item_quant = item_max;
            let item_img = $e.find("img").attr("src");
            let item_id = item_img;

            if (this.item_ids.has(item_id)) return;
            this.item_ids.add(item_id);

            let encoded_url = encodeURIComponent(item_name).replace("'", "%27");
            let url = `http://dappervolk.com/trades?search=${encoded_url}&in=item&type=all&item_type=&rarity=all&order=newest`;

            items.push({
              item_name: item_name,
              item_id: item_id,
              item_quant: item_quant,
              item_max: item_max,
              item_img: item_img,
              trade: url,
            });
          } else if ($e.is("img")) {
            let img = $e.attr("src");
            let id = img;

            if (this.item_ids.has(id)) return;
            this.item_ids.add(id);
            items.push({
              item_name: "",
              item_quant: 1,
              item_id: id,
              item_img: img
            });
          } else if ($e.is("div.inventory-item") && $e.data("name") && $e.data("name").length > 0) {
            // Selected from inventory/ trade page
            let item_name = $e.data("name");
            //let item_id = $e.data('id');
            let item_quant = $e.data("quantity");
            let item_max = $e.data("quantity");
            let item_img = $e.find("img").attr("src");
            let item_id = item_img;

            if (this.item_ids.has(item_id)) return;
            this.item_ids.add(item_id);

            let encoded_url = encodeURIComponent(item_name).replace("'", "%27");
            let url = `http://dappervolk.com/trades?search=${encoded_url}&in=item&type=all&item_type=&rarity=all&order=newest`;

            items.push({
              item_name: item_name,
              item_id: item_id,
              item_quant: item_quant,
              item_max: item_max,
              item_img: item_img,
              trade: url,
            });
          }
        });
      this.items.push(...items);
      console.debug("Items", items);
      if (items.length == 0) {
        console.log("Bad input!");
        console.debug($(this.$refs.inp).html());
      }

      $(this.$refs.inp).empty();
    },
    created() {
      //this.$refs.inp.innerHTML = this.defaultd;
    },
    selectAll(e) {
      e.preventDefault();
      $(e.target).selectText();
      return false;
    },
    selectBBCode() {
      this.$refs.bbcode.select();
      this.$refs.bbcode.setSelectionRange(0, 99999); // For mobile devices
    },
    copyBBCode() {
      this.selectBBCode();
      navigator.clipboard.writeText(this.$refs.bbcode.value);
    },
    startedit(evt, item) {
      evt.preventDefault();
      item.edit = true;
      setTimeout(() => {
        $(evt.target).siblings("textarea.item-name").focus();
      }, 10);

      //return false;
      //$(evt.target).siblings('textarea.item-name').get(0).click();
    },
    starteditquant(evt, item) {
      evt.preventDefault();
      item.edit_quant = true;
      setTimeout(() => {
        $(evt.target).siblings("input.item-quant").focus();
      }, 10);
    },
    removeItem(i) {
      this.item_ids.delete(this.items[i].item_id);
      this.items.splice(i, 1);
    }
  },
  mounted() {
    this.getItems();
  },
  components: {
    SlickList,
    SlickItem,
    HandleDirective,
    Icon
  },
  directives: { handle: HandleDirective }
};
</script>

